import { DatePipe } from '@angular/common';
import { Component, Input, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Subject } from 'rxjs';
import { AppConstants } from '../@core/constants/app-constants';
import { ProviderGroup, UserGroup } from '../@core/datamodel/userDataModel';
import { RBACINFO } from '../@core/urls/users-url.config';
import { UserOrgService } from '../@core/service/user-org.service';
import { AlertService } from '../@core/service/alert.service';
import { DataStoreService } from '../@core/service/data-store.service';

declare const $: any;

@Component({
  selector: 'users',
  templateUrl: './users.component.html',
  styleUrls: ['./users.component.scss']
})
export class UsersComponent implements OnInit {
  @Input() public RBACORG: RBACINFO = new RBACINFO();
  @Input() public PERMISSION: any;
  userList: UserGroup[] = [];
  providerList: ProviderGroup[] = [];
  filteredUserList: UserGroup[] = [];
  selectedRoles: string[] = [];
  selectedTeams: string[] = [];
  userRoles!: any[];
  userTeams!: any[];
  formSubmit: boolean;
  disabled!: boolean;
  userForm!: FormGroup;
  userSearch$ = new Subject<string>();
  deletedId!: string;
  userid: any;
  checked: boolean;
  sendCredentials: boolean;
  providerAccount: boolean;
  uploadedFile: any;
  urlPath: any;
  imageData: any;
  attachType: any;
  imageInformation!: string;
  currentDate = new Date();
  policyGroupList: any;
  orgInfo: any;
  orgId: any;
  firstname:string;
  httpService:any;
  lastname: string;
  middlename: string;
  modelFirstName: string = '';
  modelMiddleName: string = '';
  modelLastName: string = '';
  constructor(
    private userService: UserOrgService,
    private fb: FormBuilder,
    private alertService: AlertService,
    private _storeservice: DataStoreService
  ) {
    this.formSubmit = false;
    this.providerAccount = false;
    this.checked = true;
    this.sendCredentials = true;
    this.imageInformation = '';
    this.initializeForm();
  }

  ngOnInit() {
    this._storeservice.currentStore.subscribe((res: any) => {
      if(res){
        this.httpService = res['HTTPSERVICE']
      }
    })
    if(this.RBACORG['orgID']){
      this.orgId = parseInt(this.RBACORG['orgID']);
      if (this.orgId) {
        this.getUserList();
        this.getRolesList();
        this.getPolicyGroupList();
      }
    }
  }

  initializeForm() {
    this.userForm = this.fb.group({
      firstname: ['', Validators.required],
      middlename: [''],
      lastname: ['', Validators.required],
      username: ['', Validators.required],
      dob: [''],
      email: ['', [Validators.required, Validators.pattern(AppConstants.regexEmail)]],
      userroles: ['', Validators.required],
      userteams: [''],
      userpolicygroups: [''],
      organizationid: [''],
      emailtocurrentuser: false,
      isenabled: [true],
      providerId: [''],
      providerAccount: false,
      thumbnail: [''],
      islocked: [false]
    });
  }

  get formValidate() {
    return this.userForm.controls;
  }

  getUserList(_key?: string) {
    this.userService.getAllUserOrgList(this.orgId).subscribe(
      (res: any) => {
        this.filteredUserList = [];
        this.userList = res.data;
        this.filteredUserList = this.userList;
      },
      (_error: any) => this.alertService.error(AppConstants.errorMessage)
    );
  }

  getRolesList() {
    this.userService.getAllUserRole(this.orgId).subscribe(
      (res: any) => {
        this.userRoles = res['data'];
      },
      (_error: any) => this.alertService.error(AppConstants.errorMessage)
    );
  }

  getPolicyGroupList(_managementGroupId?: number) {
    this.userService.getOrgPolicyGroupList(this.orgId).subscribe(
      (res: any) => {
        this.policyGroupList = res['data'];
      },
      (err: any) => console.log(err)
    );
  }
  addUser() {
    const userData = this.userForm.getRawValue();
    delete userData.thumbnail;
    this.formSubmit = true;
    if (this.userForm.valid) {
      userData.organizationid = this.orgId;
      userData.id = 1;
      userData.userteams = userData.userteams || [];
      userData.userroles = [userData.userroles] || [];
      userData.userpolicygroups = userData.userpolicygroups || [];
      userData.emailtocurrentuser = userData.emailtocurrentuser || false;
      userData.dob = new DatePipe('en-US').transform(userData.dob, 'YYYY-MM-dd');
      if (!this.userid) {
        this.userWithOutId(userData);
      } else {
        this.userWithID(userData);
      }
    }
  }
  userWithOutId(userData: any) {
    this.userService.saveUser(userData).subscribe(
      (res: any) => {
        if (this.providerAccount && this.userForm.controls['providerId'].valid) {
          this.addProviderUser(userData.providerId, res['data']);
        }
        this.cancel();
        this.getUserList();
        this.alertService.success('User created successfully');
      },
      (error: any) => {
        if (error.error.message) {
          this.alertService.error(error.error.message);
        } else {
          this.alertService.error('Failed to create user');
        }
      }
    );
  }
  userWithID(userData: any) {
    if (this.uploadedFile) {
      userData.additionalinfo = {
        photo: this.imageData,
        thumbnail: this.imageData
      };
    }
    this.userService.updateUser(userData, this.userid).subscribe(
      () => {
        this.cancel();
        this.getUserList();
        this.alertService.success('User updated successfully');
      },
      (err: any) => {
        if (err.error.message) {
          this.alertService.error(err.error.message);
        } else {
          this.alertService.error('Failed to create user');
        }
      }
    );
  }

  mapUserToRole(userId: any, roleId: any) {
    const request = {
      userId: userId,
      roleId: roleId
    };
    this.userService.addUserRole(request).subscribe(() => console.log('User mapped to role Successfully'));
  }

  cancel() {
    if (!this.userid) {
      this.userForm.reset();
      this.userForm.patchValue({
        isenabled: true,
        islocked: false
      });
    }
    this.providerAccount = false;
    this.formSubmit = false;
    this.userForm.controls['email'].enable();
    this.userForm.controls['username'].enable();
  }

  searchUser(event: Event) {
    const value = (event.target as HTMLInputElement).value.toLowerCase();
    this.filteredUserList = this.userList.filter(a => a?.firstname?.toLowerCase().startsWith(value));
  }

  getUserInfo(user: any) {
    this.sendCredentials = false;
    this.userid = user.id;
    this.userService.getAllUserList(this.userid).subscribe((res: any) => {
      this.imageInformation = res.data.additionalinfo?.thumbnail?.fileName?.split('/')[2] || '';
      this.userForm.patchValue({
        firstname: res.data.firstname,
        middlename: res.data.middlename,
        lastname: res.data.lastname,
        username: res.data.username,
        dob: new Date(res.data.dob),
        email: res.data.email,
        organizationid: res.data.organizationid,
        thumbnail: res.data.additionalinfo?.thumbnail?.url || '',
        isenabled: res?.data?.isenabled,
        islocked: res?.data?.islocked,
        userroles: res?.data?.userroles[0]?.id || 0,
        userteams: res.data.userteams
          ? res.data.userteams
              .filter((item: any) => item)
              .map((item: any) => {
                return item['id'];
              })
          : [],
        userpolicygroups: res.data.policygroups
          ? res.data.policygroups
              .filter((item: any) => item)
              .map((item: any) => {
                return item['id'];
              })
          : []
      });

      this.userForm.controls['email'].disable();
      this.userForm.controls['username'].disable();
      // });
    });
  }

  getRolePolicyGroups(roleId: any) {
    this.userService.getRoleById(roleId).subscribe(
      (res: any) => {
        if (res) {
          this.userForm.patchValue({
            userpolicygroups:
              res['data'].policyGroups
                .filter((item: any) => item)
                .map((item: any) => {
                  return item['id'];
                }) || []
          });
        }
      },
      (_err: any) => this.alertService.error('Failed to Fetch Policy Groups')
    );
  }

  delete(event: Event, id: any) {
    event.stopPropagation();
    this.deletedId = id;
    $('#Deleteuser').modal('show');
  }

  deleteUser() {
    this.userService.deleteUser(this.deletedId).subscribe(
      () => {
        this.cancel();
        this.alertService.success('User deleted successfully');
        $('#Deleteuser').modal('hide');
        this.getUserList();
      },
      (_err: any) => this.alertService.error('Failed to delete User')
    );
  }

  cancelUser() {
    this.sendCredentials = true;
    this.userForm.reset();
    this.userForm.patchValue({
      isenabled: true,
      islocked: false
    });
    this.userForm.controls['email'].enable();
    this.userForm.controls['username'].enable();
    this.userid = '';
  }

  activateUser() {
    const userData = this.userForm.getRawValue();
    const body = {
      email: userData.email,
      code: userData.otp
    };
    this.userService.activateUser(body).subscribe(
      (_res: any) => {
        this.alertService.success('User Activated Successfully');
      },
      (_err: any) => {
        this.alertService.error('User Activation Failed');
      }
    );
  }

  selectProvider(selected: any) {
    this.providerAccount = selected;
  }

  addProviderUser(providerId: any, userid: any) {
    const data = {
      providerId: providerId,
      accountId: userid.toString()
    };
    this.userService.addProviderUser(data).subscribe(
      () => {
        console.log('Provider User added Successfully');
      },
      (_err: any) => console.log('failed to add Provider user')
    );
  }

  handleFileInput(fileValue: any) {
    const target = fileValue.target as HTMLInputElement;
    const file: File = target.files![0];
    this.uploadedFile = fileValue.target.files[0].name;
    this.imageData = {
      contentType: fileValue.target.files[0].type,
      fileName: `user-profileimage/${this.userid}/${this.uploadedFile}`
    };
    this.imageInformation = this.uploadedFile;
    if (this.validateImage(fileValue.target.files)) {
      this.userService.uploadKey(this.imageData).subscribe(
        (res: any) => {
          this.urlPath = res.data;
          const uploadAttachment: any = document.getElementById('file');
          const uploadAttachmentDetails = uploadAttachment.files[0];
          this.httpService.putUpload(this.urlPath, uploadAttachmentDetails, uploadAttachmentDetails.type).subscribe(
            (_resp: any) => {
              this.alertService.success('Uploaded Successfully!');
              const reader = new FileReader();
              reader.onload = () => {
                this.userForm.patchValue({ thumbnail: reader.result as string });
              };
              reader.readAsDataURL(file);
              this.attachType = fileValue.target.files[0].type;
            },
            (error: any) => {
              if (error.status == 0) {
                this.alertService.error(AppConstants.errorMessage);
                this.uploadedFile = '';
                this.urlPath = '';
              }
            }
          );
        },
        (_error: any) => {
          this.alertService.error(AppConstants.errorMessage);
        }
      );
    }
  }
  removeThumbnail() {
    this.userForm.controls['thumbnail'].reset();
    this.imageInformation = '';
    this.imageData = '';
  }

  validateImage(file: any) {
    const fileSize = Number(file[0].size) / 1024;
    const filetype = file[0].type.replace('image/', '');
    if (filetype === 'jpg' || filetype === 'jpeg' || filetype === 'png' || filetype === 'gif') {
      if (fileSize < 100) {
        return true;
      } else {
        this.imageInformation = 'Image size exceeds 100 KB';
        this.alertService.warn('File is bigger than 100 KB');
        return false;
      }
    } else {
      this.imageInformation = '';
      this.alertService.warn(`${filetype} format is not supported`);
      return false;
    }
  }
  stopPropagation(event: Event) {
    event.stopPropagation();
  }
  onModelFirst(value: string): void {
    this.modelFirstName = value.replace(/\w\S*/g, (txt) => txt.charAt(0).toUpperCase() + txt.substr(1).toLowerCase());
  }
  onModelMiddle(value:string):void {
    this.modelMiddleName = value.replace(/\w\S*/g, (txt) => txt.charAt(0).toUpperCase() + txt.substr(1).toLowerCase());
  }
  onModelLast(value:string):void {
    this.modelLastName = value.replace(/\w\S*/g, (txt) => txt.charAt(0).toUpperCase() + txt.substr(1).toLowerCase());
  }
}
